import React,{Component} from 'react'
import {hashHistory} from 'react-router'
import { Form, Checkbox, Row, Col, Button, Spin, message } from 'antd';
const FormItem = Form.Item;
const CheckboxGroup = Checkbox.Group;

class ManageListForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            options: [],
            defaultValue: []
        };
        ['handleSubmit'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        let optionsData = [];
        let defaultValue = [];
        this.setState({loading: true});
        ajax({
            url: "/auth/admin/getAuthList",
            method: "post",
            data: {roleId: that.props.type},
            callback: function(data) {
                if(data.status == 200) {
                    data.data.map(item => {
                        if(item.checked == true) {
                            defaultValue.push(item.id)
                        }
                        optionsData.push({
                            label: item.name,
                            value: item.id
                        })
                    });
                    that.setState({
                        loading: false,
                        options: optionsData,
                        defaultValue
                    })
                }
            }
        })
    }

    handleSubmit(e) {
        let that = this;
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                that.setState({loading: true});
                ajax({
                    url: "/auth/admin/setAuthList",
                    method: "post",
                    data: Object.assign(values, {roleId: that.props.type}),
                    callback: function(data) {
                        that.setState({loading: false});
                        if(data.status == 200) {
                            message.success("设置成功!", 1);
                        } else {
                            message.error(data.errMsg, 1);
                        }
                    }
                });
            }
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem>
                        {getFieldDecorator('authList',
                            {
                                rules: [
                                    {
                                        required: true,
                                        message: '请选择一项权限!'
                                    }
                                ],
                                initialValue: this.state.defaultValue
                            }
                        )(
                            <CheckboxGroup>
                                <Row>
                                    {this.state.options.map(item => {
                                        return <Col span={24} key={item.value}><Checkbox value={item.value}>{item.label}</Checkbox></Col>}
                                    )}
                                </Row>
                            </CheckboxGroup>
                        )}
                    </FormItem>
                    <FormItem>
                        <Button type="primary"  onClick={this.handleSubmit}>提交</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedManageListForm = Form.create({})(ManageListForm);

module.exports = WrappedManageListForm;


